import Axios from "axios"
import {ChannelItems, FetchData, VideoItems} from "@/services/VideoTypings";
import {MapData} from "@/services/MapTypings";
import Store from "../store";

export class MapAPI {
  private axios = Axios.create();
  
  constructor() {}
  
  public async getMaps(): Promise<MapData> {
    let responses = await this.axios.get<MapData>(Store.state.map_url);
    return responses.data
  }
}
