

















import Vue from "vue"
import {Component} from "vue-property-decorator"
import {MapAPI} from "@/services/MapAPI";
import {MapData} from "@/services/MapTypings";
import MapCard from "@/components/MapCard.vue";

@Component({
  name: "Maps",
  components: {
    MapCard
  }
})
export default class Maps extends Vue {
  data() {
    return {
      listMaps: []
    }
  }
  listMaps?: MapData["maps"];
  async mounted() {
    const data = new MapAPI();
    const result = await data.getMaps();
    this.listMaps = result.maps
  }
}
